/* eslint-disable no-throw-literal */
import axios, { AxiosError } from 'axios';
import moment from 'moment';

import { BASE_URL } from 'constants/constants';
import { uuid } from 'utils/externals';
import { getAnoymousToken, resetToken, signOut } from 'utils/common';
import GlobalStorage from 'constants/global';
import GlobalStore from 'constants/global';

const TIME_OUT = 180000;

const codeMessage = {
  200: 'Ly d liu thnh cng',
  201: 'Thm mi hoc sa i thnh cng',
  202: 'Thm mi hoc sa i thnh cng',
  204: 'Xa d liu thnh cng.',
  400: 'Yu cu  c pht hnh vi mt li. My ch khng to hoc sa i d liu.',
  401: 'Ngi dng khng c quyn (m thng bo, tn ngi dng, li mt khu).',
  403: 'Ngi dng c y quyn nhng quyn truy cp b cm.',
  404: 'Khng th kt ni n server',
  406: 'nh dng c yu cu khng kh dng.',
  410: 'Ti nguyn c yu cu s b xa vnh vin v s khng cn kh dng na.',
  422: ' xy ra li xc thc khi to i tng.',
  500: ' xy ra li trn my ch. Vui lng kim tra my ch.',
  502: 'Cng xu',
  503: 'The service is unavailable and the server is temporarily overloaded or maintained.',
  504: ' ht thi gian ch ca cng.'
};

export const handleFetchError = (error, endPoint, token) => {
  const { response, message, errors } = error;

  if (response) {
    const { status, data } = response;
    if ((status === 401 || status === 403) && token) {
      // Unauthorize
      signOut(true);
    }

    if (data) {
      const { error, detail, message, errors } = data;
      if (error) {
        throw error;
      }
      if (errors && errors.length > 0) {
        throw errors[0];
      }
      if (detail) {
        throw detail;
      }
      if (message) {
        throw message;
      }
    }

    if (status >= 400) {
      throw codeMessage[status] || 'C li xy ra';
    }
  }

  if (errors && errors.length > 0) {
    throw errors[0];
  }

  if (message) {
    if (message.includes('timeout of')) {
      throw 'Time out';
    }
    if (message.toString().includes('Network Error')) {
      throw 'Li kt ni, vui lng th li';
    }
    throw message;
  }

  if (error.toString().includes('Network Error')) {
    throw 'Li kt ni, vui lng th li';
  }
  throw error;
};

export const funcFetch = async (endPoint, data = null, method = 'get', headers = {}) => {
  const { token, refreshToken } = await GlobalStore;

  headers['Accept-Language'] = GlobalStore.lang === 'en' ? 'en' : 'vn';
  headers.uuid = GlobalStore.uuid
    ? await GlobalStore.uuid
    : await uuid();

  if (refreshToken && endPoint === '/users/refresh-access-token') {
    headers = {
      ...headers,
      Authorization: 'Bearer ' + refreshToken,
    };
  }

  if (token && !['/common/tk', '/users/refresh-access-token'].includes(endPoint)) {
    headers = {
      ...headers,
      Authorization: 'Bearer ' + token,
    };
  }

  return axios({
    url: BASE_URL + endPoint,
    method,
    headers,
    data,
    timeout: TIME_OUT,
  })
    .then((response) => {
      if (response) {
        const d = response.data;
        

        const { code, msg, data } = d;

        if (code === 40027) {
          return { apiCode: 40027 };
        }
        if (code < 300) {
          return data;
        }

        throw new Error(msg);
      }
    })
    .catch(async (error) => {
      console.log(error, endPoint, token);
      handleFetchError(error, endPoint, token);
    });
};

const fetchApi = async (endPoint = '', data = null, method = 'get', headers = {}) => {
  const { token, tokenExpired, isLoginUser } = await GlobalStorage;

  if (token && tokenExpired) {
    const isTokenExpired = moment().unix() > moment(tokenExpired).unix();
    if (isTokenExpired) {
      if (isLoginUser) {
        return resetToken().then(res => {
          if (res && endPoint) {
            return funcFetch(endPoint, data, method, headers);
          }
        });
      } else {
        return getAnoymousToken().then(res => {
          if (res && endPoint) {
            return funcFetch(endPoint, data, method, headers);
          }
        });
      }
    } else {
      return funcFetch(endPoint, data, method, headers);
    }
  } else {
    console.log('no token');

    if (endPoint === '/common/tk') {
      return funcFetch(endPoint, data, method, headers);
    }
    
    throw 'No token';
  }
};

export default fetchApi;
